import { Grid, Paper, Typography } from '@material-ui/core'
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import unBounce from './unbounce.PNG';
import getHarvest from './getharvest.png';
import strawberry from './StrawBerry.jfif'
import styled from 'styled-components';
import react_image from './logo192.png';
import styles from './Projects.module.css';
import GitHubIcon from '@mui/icons-material/GitHub';

// const unBounce =`https://cdn.hashnode.com/res/hashnode/image/upload/v1612848879266/klkUa6KXQ.gif?auto=format,compress&gif-q=60&format=webm`

const Btns = styled.div`
    display:grid;
    width:80%;
    margin:auto;
    grid-template-columns: 10% 80% 10%;
    grid-template-rows: 1;
    grid-template-areas: "github techstack live";
    justify-content: center;
   @media screen and (max-width: 600px) {
    grid-template-columns: 100%;
    grid-template-rows: 3;
    grid-template-areas:"techstack"
                        "github" 
                         "live";

   }
`
const Hide = styled.div`
  &>input{
      border:none;
      background:none;
      outline:none;
      cursor:none;
  }
  &>input:focus{
      visibility:hidden;
  //  display:none;
}
`

const Demo = styled.button`


min-width: 58px;
margin-top: 10px;
border-radius: 12px;
background-color: rgba(27,27,27,.91);
    border  :none;
    height: 50px;
    width: 25%;  
    font-size: 14px;
    @media (max-width: 600px) {
      width: 100%;
      
  }
   
`
const Live = styled.button`

grid-area: "live";
min-width: 30px;
margin-top: 10px;
border-radius: 12px;
background-color: rgba(27,27,27,.91);
border  :none;
    height: 50px;
    /* width: 10%; */
    font-size: 14px;
    @media (max-width: 600px) {
      width: 100%;   
  }
   
`
const GitHub = styled.button`

grid-area: "github";
min-width: 30px;
margin-top: 10px;
border-radius: 12px;
background-color: rgba(27,27,27,.91);
background-color: none;


 
    border  :none;
    height: 50px;
    /* width: 10%; */
    
    font-size: 14px;
    @media (max-width: 600px) {
      width: 100%;
      
  }
   
`
const Section = styled.div`
    height:30%;
    width: 80%;
    margin:20px auto;
  
    &>p{
     
      font-size: 24px;
    }
 `
const TechStack = styled.div`
grid-area: "techstack";
display: flex;
align-items: center;
justify-content: center;

    
 `

const Heading = styled.div`
 color:#4c4c4c;
 width:20%;
 min-width: 200px;
 margin:10px auto;
 padding-bottom: 5px;
 border-bottom: 5px solid #4c4c4c;
 `

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    margin: 'auto',
  },
  paper: {
    // height: 400,
    width: "90%",
    paddingBottom: "20px",
    margin: "auto",
    borderRadius: "10px",
    '& img': {
      borderRadius: "10px 10px 0px 0px",
    }
  },
  control: {
    padding: theme.spacing(2),
  },
}));
export const Projects = ({ projects }) => {
  // const classes = useStyles();

  const [spacing, setSpacing] = React.useState(2);
  const classes = useStyles();

  const handleChange = (event) => {
    setSpacing(Number(event.target.value));
  };

  return (
    <div className={styles.mainCOntainer}>
      <Heading><h1>Projects</h1></Heading>
      <Hide>
        <input ref={projects}></input>
      </Hide>
      <Grid container className={classes.root} spacing={3}>
        <Grid item xs={12}>
          <Grid container justifyContent="center" spacing={8}>
            <Grid item>
              <Paper className={classes.paper}>
                <img width="100%" src={getHarvest} alt="" />
                <Section>
                  <h2>GETHARVEST CLONE</h2>
                  <p>This website is a clone of task and expense management website, In this, we have implemented some features like
                    an organization can keep track of all the employee's work done and expenses. Also, they can download invoices in pdf so that It will be easier for organizations to get their employee's payments done
                  </p>

                </Section>
                <Btns>

                  <GitHub onClick={(e) => {
                    e.preventDefault();

                    window.open('https://github.com/pujarianil4/getharvest_clone', '_blank');
                  }}><GitHubIcon /></GitHub>
                  <TechStack>


                    <img width="30px" src="https://img.icons8.com/color/48/000000/css3.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/html-5.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/javascript--v1.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/redux.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/plasticine/452/react.png" alt="N/A" />

                  </TechStack>
                  <Live

                    onClick={(e) => {
                      e.preventDefault();

                      window.open('https://getharvestclone.netlify.app', '_blank');
                    }}
                  >Live</Live>
                  {/* <Demo

                    onClick={(e) => {
                      e.preventDefault();


                      window.open('https://masai-course.s3.ap-south-1.amazonaws.com/users/768/submissions/84078/218390/05408a678699dd875605a25b8f9780b7/Getharvest_Demo.mp4', '_blank');
                    }}
                  >Demo</Demo> */}
                </Btns>
              </Paper>

            </Grid>
            <Grid item>
              <Paper className={classes.paper}>


                <img width="100%" src={strawberry} alt="" />
                <Section>
                  <h2>STRAWBERRYNET CLONE</h2>
                  <p>This website is a clone of an e-commerce website, We build some exciting features in this like we have a real payment gateway with stripe
                    we also build an admin panel to view and update things from the other side, and some general features for the users like they can purchase and save their preferences to cart </p>

                </Section>
                <Btns>
                  <GitHub onClick={(e) => {
                    e.preventDefault();

                    window.open('https://github.com/makkardeepak25/strawberry_net_clone', '_blank');
                  }}><GitHubIcon /></GitHub>
                  <TechStack>
                    <img width="30px" src="https://img.icons8.com/color/48/000000/css3.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/html-5.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/javascript--v1.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/50/000000/mongodb.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/redux.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/plasticine/452/react.png" alt="N/A" />


                    <img width="30px" src="https://img.icons8.com/color/48/000000/nodejs.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/ios-filled/50/000000/stripe.png" alt="N/A" />
                  </TechStack>
                  <Live
                    onClick={(e) => {
                      e.preventDefault();

                      window.open('https://strawberynetclone.netlify.app', '_blank');
                    }}
                  >Live</Live>
                  {/* <Demo
                    onClick={(e) => {
                      e.preventDefault();

                      window.open('https://masai-course.s3.ap-south-1.amazonaws.com/users/768/submissions/107532/253481/a50f9f85d8c91701b3d4a313551ec0bc/StrawBerry_full.mp4', '_blank');
                    }}
                  >Demo</Demo> */}

                </Btns>
              </Paper>
            </Grid>
            <Grid item>
              <Paper className={classes.paper}>
                <img width="100%" src={unBounce} alt="" />
                <Section>
                  <h2>UNBOUNCE CLONE</h2>
                  <p>This is a clone drag-and-drop builder, We have built some static pages to look similar to the original one.
                  </p>

                </Section>
                <Btns>
                  <GitHub onClick={(e) => {
                    e.preventDefault();

                    window.open('https://github.com/Amol-Talekar/unbounce', '_blank');
                  }}><GitHubIcon /></GitHub>
                  <TechStack>

                    <img width="30px" src="https://img.icons8.com/color/48/000000/css3.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/html-5.png" alt="N/A" />
                    <img width="30px" src="https://img.icons8.com/color/48/000000/javascript--v1.png" alt="N/A" />

                  </TechStack>
                  <Live
                    onClick={(e) => {
                      e.preventDefault();

                      window.open('https://amol-talekar.github.io/unbounce/', '_blank');

                    }}
                  >Live</Live>

                  {/* <Demo
                    onClick={(e) => {
                      e.preventDefault();

                      window.open('https://cdn.hashnode.com/res/hashnode/image/upload/v1612848879266/klkUa6KXQ.gif?auto=format,compress&gif-q=60&format=webm', '_blank');
                    }}
                  >Demo</Demo> */}
                </Btns>
              </Paper>
            </Grid>

          </Grid>
        </Grid>
      </Grid>



    </div>
  )
}


