import React, { useRef } from "react";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import ButtonAppBar from "../AppBar/AppBarS";

import styles from "./Mainpage.module.css";
import { AnimatedSummary } from "../AnimatedSummary/AnimatedSummary";
import { ContactForm } from "../Contact/ContactForm";
import { Skills } from "../Skills/Skills";
import GitHubCalendar from "react-github-calendar";
import { Projects } from "../Projects/Projects";
import styled from "styled-components";
import { AboutMe } from "../About/AboutMe";


const Gapping = styled.div`
  margin: auto;
  height: 50vh;

  // background: linear-gradient(#dbf4f1 100%, #ffed4b 50%);

  @media (max-width: 1024px) {
    height: auto;
    width: 50%;
    padding-bottom: 100px;
  }
`;
const Profile = styled.div`
  margin: auto;
  height: 100vh;
  // border-radius: 20% 20% 0% 0%;
  // background:#fff;
  @media (max-width: 1024px) {
    height: auto;
    padding-bottom: 100px;
  }
`;

const Heading = styled.div`
  // color:#c4c4c4;
  color: #4c4c4c;
  width: 20%;
  min-width: 200px;
  margin: 10px auto;
  padding-bottom: 5px;
  // border-bottom: 5px solid #c4c4c4;
  border-bottom: 5px solid #4c4c4c;
`;

const Hide = styled.div`
  & > input {
    border: none;
    background: none;
    outline: none;
    cursor: none;
  }
  & > input:focus {
    visibility: hidden;
    //  display:none;
  }
`;
export default function Mainpage() {
  const small = useMediaQuery("(max-width:1024px)");
  const large = useMediaQuery("(min-width:1025px)");
  const exampleTheme = {
    background: "transparent",

    // grade4: 'hsl(338, 78%, 30%)',
    // grade3: 'hsl(338, 78%, 44%)',
    // grade2: 'hsl(338, 78%, 58%)',
    // grade1: 'hsl(338, 78%, 72%)',
    grade0: "rgba(27,27,27,.91)",
    text: "#4c4c4c",
    color: "#4c4c4c",
  };

  const skills = useRef(null);
  const projects = useRef(null);
  const github = useRef(null);
  const contact = useRef(null);
  const onButtonClick = (target) => {
    target.current.focus();
  };

  return small ? (
    <div>
      <ButtonAppBar
        onButtonClick={onButtonClick}
        skills={skills}
        projects={projects}
        contact={contact}
      />
      <Profile>
        <AnimatedSummary />
      </Profile>
      <AboutMe />
      <Skills skills={skills} />
      <Projects projects={projects} />
      <Gapping>
        {/* <Heading>
          <h1>Github Contribution</h1>
        </Heading>
        <GitHubCalendar
          username="kamalgupta97"
          blockSize={15}
          theme={exampleTheme}
          fullYear={false}
          blockMargin={4}
          fontSize={16}
        /> */}
      </Gapping>

      <ContactForm contact={contact} />
    </div>
  ) : large ? (
    <div className={styles.mediawrapper}>
      <ButtonAppBar
        home={"Home"}
        onButtonClick={onButtonClick}
        skills={skills}
        projects={projects}
        contact={contact}
      />

      <Profile>
        <AnimatedSummary home={"Home"} />
      </Profile>

      <AboutMe />
      <Skills skills={skills} />

      <Projects projects={projects} />

      <Gapping>
        <Hide>
          <input ref={github}></input>
        </Hide>
        {/* <Heading>
          <h1>Github Contribution</h1>
        </Heading>
        <GitHubCalendar
          username="kamalgupta97"
          blockSize={15}
          theme={exampleTheme}
          fullYear={false}
          blockMargin={4}
          fontSize={16}
        /> */}
      </Gapping>


      <ContactForm contact={contact} />
    </div>
  ) : (
    <div></div>
  );
}
