import React from 'react';
import styled from "styled-components";
import styles from './AboutMe.module.css';
import url_cartoon from './cartoon.png'
const About = styled.div`
  margin: auto;
  height: 100vh;

background:#fff;
  @media (max-width: 1024px) {
    height: auto;
    padding-bottom: 100px;
  }
`
export const AboutMe = () => {
  return (
    <div className={styles.about_Wrapper_parent}>

      <div className={styles.about_Wrapper}>
        <div className={styles.about_heading}>
          <h1>About</h1>

        </div>
        <div className={styles.imgContainerParent}>
          <div className={styles.imgContainer}>
            <img src={url_cartoon} alt="" width={"100%"} />
          </div>
          <div className={styles.aboutmecontainer}>
            <p className={styles.aboutme}>
              A passionate Full Stack Software Developer, having experience of building Web applications with JavaScript / Reactjs / Nodejs / MongoDb and some other cool libraries and frameworks.
            </p>
          </div>
        </div>

      </div>
    </div>
  )
}
